import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";


function Login() {
  const fields = {
    email: "",
    password: "",
  };
  const [values, setValues] = useState(fields);
  const [errors, setErrors] = useState(fields);
  const [submitted, setSubmitted] = useState(false);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    handleValidate(values);
  }, [values]);

  const handleChange = (event) => {
    setValues({
      ...values,
      [event.target.name]: event.target.value,
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    setSubmitted(true);
    if (handleValidate(values)) {
      setLoading(true);
   
    }
  };

  const handleValidate = (values) => {
    let errors = {};
    let isValid = true;
    if (typeof values["email"] !== "undefined") {
      let pattern = new RegExp(
        /^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+))|("[\w-\s]+")([\w-]+(?:\.[\w-]+)))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i
      );
      if (!pattern.test(values["email"])) {
        isValid = false;
        errors["email"] = "Please enter valid email address.";
      }
    }
    if (!values["email"]) {
      isValid = false;
      errors["email"] = "Please enter email address.";
    }
    if (typeof values["password"] !== "undefined") {
      if (values["password"].length != 6 && values["password"].length < 6) {
        isValid = false;
        errors["password"] = "Password must be at least 6 characters long.";
      }
    }
    if (!values["password"]) {
      isValid = false;
      errors["password"] = "Please enter password.";
    }
    setErrors(errors);
    return isValid;
  };
  let inputs = JSON.parse(localStorage.getItem(inputs))
  return (
    <div className="col-md-6 col-md-offset-3 ">
      <h2>Login</h2>
      <form name="form" onSubmit={handleSubmit}>
        <div>
          <label htmlFor="email">Email</label>
          <input
            type="text"
            name="email"
            value={values.email}
            onChange={handleChange}
            className={
              "form-control" +
              (submitted && !!errors.email ? " is-invalid" : "")
            }
          />
          {submitted && !!errors.email && (
            <div className="inline-errormsg">{errors.email}</div>
          )}
        </div>
        <div>
          <label htmlFor="password">Password</label>
          <input
            type="password"
            name="password"
            value={values.password}
            onChange={handleChange}
            className={
              "form-control" +
              (submitted && !!errors.password ? " is-invalid" : "")
            }
          />
          {submitted && !!errors.password && (
            <div className="inline-errormsg">{errors.password}</div>
          )}
        </div>
        <div className="form-group">
          <button type="submit" className="btn btn-primary">
            Login
          </button>
          {loading && (
            <img src="data:image/gif;base64," />
          )}
          <Link to="/register" className="btn btn-link">
            Register
          </Link>
        </div>
      </form>
    </div>
  );
}

export default Login;